// frontend/src/components/services/ServiceCard.tsx
import { ArrowRight } from 'lucide-react';
import Link from 'next/link';
import { cn } from '@/lib/utils/cn';

interface ServiceCardProps {
  service: {
    id: string;
    title: string;
    slug: string;
    description: string;
    icon: string;
    color: string;
    features?: string[];
    stats?: Array<{ value: string; label: string }>;
  };
  variant?: 'default' | 'compact' | 'featured';
}

const ServiceCard = ({ service, variant = 'default' }: ServiceCardProps) => {
  // Mapping des couleurs aux classes Tailwind
  const colorClasses: Record<string, string> = {
    blue: 'bg-blue-50 text-blue-700 border-blue-200 hover:bg-blue-100',
    purple: 'bg-purple-50 text-purple-700 border-purple-200 hover:bg-purple-100',
    green: 'bg-green-50 text-green-700 border-green-200 hover:bg-green-100',
    orange: 'bg-orange-50 text-orange-700 border-orange-200 hover:bg-orange-100',
    indigo: 'bg-indigo-50 text-indigo-700 border-indigo-200 hover:bg-indigo-100',
    red: 'bg-red-50 text-red-700 border-red-200 hover:bg-red-100'
  };
  
  const colorClass = colorClasses[service.color] || colorClasses.blue;
  
  const link = `/services/${service.slug}`;
  
  if (variant === 'compact') {
    return (
      <Link 
        href={link}
        className="block group"
      >
        <div className="bg-white rounded-xl border border-gray-200 p-6 hover:shadow-lg transition-all duration-300 hover:border-primary-200">
          <div className="flex items-start gap-4">
            <div className={cn(
              "w-12 h-12 rounded-lg flex items-center justify-center text-2xl flex-shrink-0",
              colorClass.split(' ')[0],
              colorClass.split(' ')[1]
            )}>
              {service.icon}
            </div>
            <div>
              <h3 className="font-semibold text-gray-900 group-hover:text-primary-600 transition-colors mb-2">
                {service.title}
              </h3>
              <p className="text-sm text-gray-600 line-clamp-2">
                {service.description}
              </p>
            </div>
          </div>
        </div>
      </Link>
    );
  }
  
  if (variant === 'featured') {
    return (
      <div className="relative group">
        <div className="absolute -inset-4 bg-gradient-to-r from-primary-500 to-secondary-500 rounded-2xl blur opacity-0 group-hover:opacity-20 transition-opacity duration-500"></div>
        <div className="relative bg-white rounded-xl border border-gray-200 overflow-hidden h-full">
          {/* Icon Header */}
          <div className={cn(
            "p-6 border-b",
            colorClass.split(' ')[0],
            colorClass.split(' ')[2]
          )}>
            <div className="flex items-center justify-between">
              <div className="text-3xl">{service.icon}</div>
              <ArrowRight className="w-5 h-5 opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
            </div>
          </div>
          
          {/* Content */}
          <div className="p-6">
            <h3 className="text-xl font-bold text-gray-900 mb-3 group-hover:text-primary-600 transition-colors">
              {service.title}
            </h3>
            <p className="text-gray-600 mb-6">
              {service.description}
            </p>
            
            {service.features && service.features.length > 0 && (
              <ul className="space-y-2 mb-6">
                {service.features.slice(0, 3).map((feature, index) => (
                  <li key={index} className="flex items-center text-sm text-gray-700">
                    <div className="w-1.5 h-1.5 rounded-full bg-green-500 mr-2"></div>
                    {feature}
                  </li>
                ))}
              </ul>
            )}
            
            {service.stats && (
              <div className="grid grid-cols-3 gap-4 mt-6 pt-6 border-t border-gray-100">
                {service.stats.map((stat, index) => (
                  <div key={index} className="text-center">
                    <div className="text-lg font-bold text-gray-900">{stat.value}</div>
                    <div className="text-xs text-gray-500">{stat.label}</div>
                  </div>
                ))}
              </div>
            )}
          </div>
          
          {/* Footer */}
          <div className="px-6 pb-6">
            <Link 
              href={link}
              className="inline-flex items-center text-primary-600 font-semibold hover:text-primary-700 transition-colors"
            >
              <span>D√©couvrir ce service</span>
              <ArrowRight className="w-4 h-4 ml-2 group-hover:translate-x-1 transition-transform" />
            </Link>
          </div>
        </div>
      </div>
    );
  }
  
  // Default variant
  return (
    <div className="bg-white rounded-xl border border-gray-200 overflow-hidden hover:shadow-xl transition-all duration-300 h-full flex flex-col">
      {/* Icon Header */}
      <div className={cn(
        "p-8 border-b",
        colorClass.split(' ')[0],
        colorClass.split(' ')[2]
      )}>
        <div className="flex items-center justify-between">
          <div className="text-4xl">{service.icon}</div>
          <ArrowRight className="w-6 h-6 opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
        </div>
      </div>
      
      {/* Content */}
      <div className="p-8 flex-grow">
        <h3 className="text-xl font-bold text-gray-900 mb-3 group-hover:text-primary-600 transition-colors">
          {service.title}
        </h3>
        <p className="text-gray-600 mb-6">
          {service.description}
        </p>
        
        {service.features && service.features.length > 0 && (
          <ul className="space-y-3 mb-8">
            {service.features.slice(0, 4).map((feature, index) => (
              <li key={index} className="flex items-center text-sm text-gray-700">
                <svg className="w-4 h-4 mr-3 text-green-500 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                </svg>
                {feature}
              </li>
            ))}
          </ul>
        )}
      </div>
      
      {/* Footer */}
      <div className="p-8 pt-0">
        <Link 
          href={link}
          className="inline-flex items-center text-primary-600 font-semibold hover:text-primary-700 transition-colors group"
        >
          <span>Explorer ce service</span>
          <ArrowRight className="w-4 h-4 ml-2 group-hover:translate-x-1 transition-transform" />
        </Link>
      </div>
    </div>
  );
};

export default ServiceCard;